html {
  font-size: 20px;
}

@media screen and (max-width: 768px) {
  nav .markdown {
    grid-template-rows: repeat(4, auto);
    grid-template-columns: 1fr;
    display: grid;
  }

  nav .markdown .subcontainer {
    display: grid;
  }

  nav .markdown input:not(:checked) ~ .subcontainer {
    display: none;
  }

  nav .markdown input:checked + label {
    text-decoration: underline;
  }
}

.reading-width {
  max-width: 60ch;
  padding: var(--grail-layout-gap-inline);
}

.burger {
  appearance: none;
  width: 48px;
  height: 48px;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: .25rem;
  display: block;
}

.burger:not(.burger-expanded):hover {
  background-color: var(--theme-body-background-medium);
}

.burger .burger-top, .burger .burger-middle, .burger .burger-bottom {
  border-top: 4px solid #000;
  border-radius: 4px;
  transition: all 1s cubic-bezier(.175, .885, .32, 1.175);
  display: block;
}

.burger .burger-middle, .burger .burger-bottom {
  margin-top: 4px;
}

.burger.burger-expanded * {
  border-color: #fff !important;
}

.burger.burger-expanded .burger-top {
  transform: rotate(45deg)translate(5px, 5px);
}

.burger.burger-expanded .burger-middle {
  opacity: 0;
}

.burger.burger-expanded .burger-bottom {
  transform: rotate(135deg)translate(-6px, 7px);
}

.z-index-100 {
  z-index: 100;
}

.z-index-101 {
  z-index: 101;
}

.modal {
  place-items: center;
  display: grid;
  position: fixed;
  inset: 0;
}

.modal .modal-content {
  border: 1px solid var(--theme-border);
  min-width: 60vw;
  max-width: 90vw;
  max-height: 85vh;
  background-color: #fff;
  border-radius: .375rem;
  padding: 1rem;
}

